// ---------- Contact Form ----------
const ContactForm: React.FC<{ onFocus: () => void; onBlur: () => void }> = ({
  onFocus,
  onBlur,
}) => {
  const [formData, setFormData] = useState({
    name: "",
    phone: "",
    email: "",
    message: "",
  });
  const [okMsg, setOkMsg] = useState(false);
  const [isSubmitting, setIsSubmitting] = useState(false);

  const handleChange = (
    e: React.ChangeEvent<HTMLInputElement | HTMLTextAreaElement>
  ) => {
    setFormData({ ...formData, [e.target.name]: e.target.value });
  };

  const validateForm = () => {
    if (!formData.name.trim()) return "Please enter your name";
    if (!formData.phone.trim()) return "Please enter your phone number";
    if (!formData.email.match(/^[^\s@]+@[^\s@]+\.[^\s@]+$/)) return "Please enter a valid email";
    return null;
  };

  const handleSubmit = async (e: React.FormEvent) => {
    e.preventDefault();
    
    const validationError = validateForm();
    if (validationError) {
      alert(validationError);
      return;
    }

    setIsSubmitting(true);
    const ok = await submitGoogleForm({
      id: "1FAIpQLSdoSJv57tLdEFGNhvJKIvQe6vlIRknTGqCXnT21hjU8H1dSDg",
      ...formData,
      pagePath: typeof window !== "undefined" ? window.location.pathname : "/roofing-in-83709",
      campaign: "bravo-roofing-boise-83709",
    });
    
    setIsSubmitting(false);
    if (ok) {
      setFormData({ name: "", phone: "", email: "", message: "" });
      setOkMsg(true);
      setTimeout(() => setOkMsg(false), 5000);
    } else {
      alert("We're having trouble sending your message. Please call us directly at (208) 515-9425.");
    }
  };

  return (
    <div className="contact-form-container">
      <h3 className="form-title">Get Your Free Roof Estimate</h3>
      <form className="contact-form" onSubmit={handleSubmit} noValidate>
        <div className="form-group">
          <input
            name="name"
            type="text"
            placeholder="Full Name"
            value={formData.name}
            onChange={handleChange}
            onFocus={onFocus}
            onBlur={onBlur}
            required
            aria-required="true"
          />
        </div>
        <div className="form-group">
          <input
            name="phone"
            type="tel"
            placeholder="Best Phone Number to Reach You"
            value={formData.phone}
            onChange={handleChange}
            onFocus={onFocus}
            onBlur={onBlur}
            required
            aria-required="true"
          />
        </div>
        <div className="form-group">
          <input
            name="email"
            type="email"
            placeholder="Your Email Address"
            value={formData.email}
            onChange={handleChange}
            onFocus={onFocus}
            onBlur={onBlur}
            required
            aria-required="true"
          />
        </div>
        <div className="form-group">
          <textarea
            name="message"
            placeholder="Tell us about your roofing needs (roof leak, replacement, repair...)"
            rows={4}
            value={formData.message}
            onChange={handleChange}
            onFocus={onFocus}
            onBlur={onBlur}
          />
        </div>
        <button 
          type="submit" 
          className={`submit-btn btn-primary ${isSubmitting ? 'submitting' : ''}`}
          disabled={isSubmitting}
        >
          <span className="btn-icon" aria-hidden="true">üìã</span>
          <span className="btn-text">
            {isSubmitting ? 'Sending...' : 'Get My Free Estimate'}
          </span>
          <div className="btn-shine"></div>
        </button>
      </form>

      {okMsg && (
        <div className="success-message" role="alert">
          <CheckCircleIcon />
          Thank you! We'll be in touch within 24 hours to discuss your project.
        </div>
      )}
    </div>
  );
};

